import { ColorPicker } from "@ali/we-design-next";

export function StopEvent(event: React.MouseEvent) {
  event.stopPropagation()
}

export function keyToPreset(key: string) {
  return key === ColorPicker.PRESETS.ART.key ? 'art' : 'normal';
}

export function presetToKey(preset: 'art' | 'normal') {
  return preset === 'art' ? ColorPicker.PRESETS.ART.key : ColorPicker.PRESETS.DEFAULT.key;
}